.exhibition-detail-page {
  position: relative;
  width: 100%; 
  min-width: 340px; /* 최소 폭 설정 */
  max-width: 1200px;
  min-height: 100vh;
  background: #FFFFFF;
  margin: 0 auto; /* 중앙 정렬 */
  font-family: 'Pretendard Variable', sans-serif;
  /* 375px~1200px 사이에서 24px~50px로 부드럽게 변화 */
  padding: 40px clamp(24px, 1.212rem + 1.15vw, 50px) 50px clamp(24px, 1.212rem + 1.15vw, 50px);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

/* 상단 타이틀 + 메뉴 바 */
.exhibition-detail-top-bar {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}

.exhibition-detail-title {
  font-size: 32px;
  font-weight: 400;
  color: #000;
  margin-bottom: 0;
  line-height: 38px;
}

.menu-icon {
  width: 23px;
  height: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  margin-top: 12px;
}

.menu-icon .line {
  width: 23px;
  height: 2px;
  background-color: black;
}

.exhibition-detail-subtitle {
  font-size: 20px;
  font-weight: 400;
  color: #000;
  margin-top: 5px; /* 40px -> 5px */
  margin-bottom: 20px;
}

/* 날짜, 장소 라벨 */
.exhibition-detail-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.exhibition-detail-date,
.exhibition-detail-location {
  background: #000;
  color: #FFF;
  font-size: 11px;
  padding: 2px 10px;
  border-radius: 20px;
  width: fit-content;
}

/* 설명 */
.exhibition-detail-description {
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.05em;
  color: #6E6E6E;
  text-align: left;
  margin-top: 20px;
  margin-bottom: 38px;
}

/* 이미지 목록 - Masonry 레이아웃 적용 */
.exhibition-detail-images {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 30px;
}

.exhibition-detail-column {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

/* 반응형 열 표시 제어 (JS 로드 전 방어 로직) */
@media (max-width: 531px) {
  .exhibition-detail-column:nth-child(2),
  .exhibition-detail-column:nth-child(3) {
    display: none;
  }
}

@media (min-width: 532px) and (max-width: 1079px) {
  .exhibition-detail-column:nth-child(3) {
    display: none;
  }
}


/* 각 이미지 박스 */
.exhibition-detail-image-container {
  width: 100%;
}

.exhibition-detail-image {
  width: 100%;
  height: auto;
  /* background: #D9D9D9; 제거 */
  display: block;
  object-fit: cover;
}

.media-container {
  width: 100%;
  position: relative;
  padding-bottom: 56.25%; /* 16:9 비율 */
  height: 0;
  overflow: hidden;
}

.media-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* 작품 정보 */
.exhibition-detail-work-info {
  margin-top: 12px;
}

.exhibition-detail-work-title {
  font-size: 12px;
  line-height: 14px;
  color: #6E6E6E;
}

.exhibition-detail-work-meta {
  margin-top: 5px;
  font-size: 12px;
  line-height: 14px;
  color: #6E6E6E;
}

/* 반응형 설정 */
@media (min-width: 768px) {
  .exhibition-detail-images {
    gap: 40px;
  }
  .exhibition-detail-column {
    gap: 40px;
  }
  
  /* 3/4 폭 제한 */
  .exhibition-detail-description {
    max-width: 75%;
  }

  /* 부제목, 날짜, 장소, 설명글만 1px 키움 */
  .exhibition-detail-subtitle { font-size: 21px; }
  .exhibition-detail-date, .exhibition-detail-location { font-size: 12px; }
  .exhibition-detail-description { font-size: 13px; }
  /* 작품 정보(타이틀, 메타)는 고정 */
}

@media (min-width: 1080px) {
  .exhibition-detail-images {
    gap: 50px;
  }
  .exhibition-detail-column {
    gap: 50px;
  }

  /* 부제목, 날짜, 장소, 설명글만 1px 더 키움 */
  .exhibition-detail-subtitle { font-size: 22px; }
  .exhibition-detail-date, .exhibition-detail-location { font-size: 13px; }
  .exhibition-detail-description { font-size: 14px; }
}

/* Skeleton UI를 위한 추가 스타일 */
.skeleton-badge {
  background-color: #f0f0f0; /* 스켈레톤 기본 색상 */
  border-radius: 20px;
}